
#app2 > .tab-bar{
    display: flex;
}
#app2 .tab-bar > li{
    border: 1px solid #dddddd;
    width: 50%;
}
#app2 .tab-bar > li.selected{
    background: rgb(160, 233, 248);
    color: white;
}
#app2 .content-bar > li{
    display: none;
}
#app2 .content-bar > li.active{
    display: block;
}


